export default {
    data() {
        return {
            flag: false,
            verify: false,
            sha: false,
            selected: {},
            serverList: [],
            numb: [],
            lens: 0
        };
    },
    onShow() {
        var _this = this;
        uni.request({
            url: this.url + 'usermachine/transferlist/',
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                console.log(res);
                console.log(res.data.data[1]);
                _this.serverList = res.data.data[1];
                _this.numb = res.data.data[0];
                console.log(_this.numb);
                let lens = 0;
                for (let i = 0; i < res.data.data[1].length; i++) {
                    let item = res.data.data[1][i];
                    console.log(item.lens);
                    lens += item.lens;
                }
                _this.lens = lens;
                console.log(_this.serverList.length);
                if (_this.numb == 0) {
                    _this.verify = true;
                } else {
                    _this.verify = false;
                }
                if (_this.serverList.length === 0) {
                    _this.flag = true;
                }
                if (res.statusCode == 400) {
                    uni.showToast({
                        title: '用户名实名认证未通过或未设置资金密码或',
                        icon: 'none'
                    });
                }
                if (res.statusCode == 204) {
                    uni.showToast({
                        title: '由服务器未验收',
                        icon: 'none'
                    });
                }
            }
        });
    },
    methods: {
        prompt: function() {
            this.sha = true;
            console.log(this.sha);
            // 先解决bug把  不然没法走流程那个红色的提示 点击出弹框 但是现在不出了也不报错不知道为啥
            // 上面显示是true但是没显示  上面设置true就谈一下
        },
        validation: function() {
            uni.request({
                url: this.url + 'usermachine/machinestatus/',
                method: 'POST',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    console.log(res);
                    if (res.statusCode == 200) {
                        uni.showToast({
                            title: '已验证'
                        });
                    }
                }
            });
            this.sha = false;
            this.verify = false;
        },
        handleSelectRadio(item) {
            if (item.id === this.selected.id) {
                this.selected = {};
            } else {
                this.selected = item;
            }
        },
        btn: function(type) {
            if (!this.selected.id) {
                //没有选中，可以弹框提示
                uni.showToast({
                    title: '请选中服务器',
                    icon: 'none'
                });
                return false;
            }
            uni.request({
                url: this.url + 'usermachine/transfer/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    console.log(res);
                }
            });
            uni.navigateTo({
                url: '../server/server?type=' + type + '&info=' + JSON.stringify(this.selected)
            });
        },
        onNavigationBarButtonTap: function() {
            uni.navigateTo({
                url: '../record/record'
            });
        }
    }
};
